import React, { Component } from "react";
import {
  CCard,
  CCardHeader,
  CCol,
  CRow,
  CCardBody,
  CButton,
  CImg,
  CDataTable,
  CContainer,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalBody,
  CModalFooter,
} from "@coreui/react";
import { FileDrop } from "react-file-drop";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import CIcon from "@coreui/icons-react";
import { Link } from "react-router-dom";
const styles1 = { border: "1px solid black", color: "black", padding: 20 };

export default class Banner extends Component {
  constructor() {
    super();
    this.state = {
      bannerList: [],
      error_login: false,
      error_message: "",
      is_logged_in: false,
      alert_login: false,
      imageBanner: "",
      noImageAvailable:
        "data:image/jpeg;base64,",
    };
  }

  componentDidMount() {
    this.bannerlist();
  }
  toggleStateModal = () => {
    this.setStateModal(!this.state.stateModal);
  };
  setStateModal(stateModal) {
    this.setState({
      stateModal: stateModal,
    });
  }
  bannerlist = () => {
    fetch(
      "https://api.aktiv.co.id/api/aktivengine/v1/setting/banner/list",
      {
        method: "GET",
        headers: new Headers({
          "Content-Type": "application/json",
          Authorization: localStorage.getItem("token"),
        }),
      }
    )
      .then((response) => response.json())
      .then((data) => {
        var bannerList = data.data.items;
        this.setState({
          bannerList: bannerList,
        });
      })
      .catch((error) => console.log(error));
  };
  convertToBase64(data) {
    const reader = new FileReader();
    reader.onload = (e) => {
      this.setState({
        imageBanner: e.target.result,
      });
    };
    reader.readAsDataURL(data);
  }
  handleInputChange = (event) => {
    var k = event.target.id;
    this.setState({
      [k]: event.target.value,
    });
  };
  addBanner=() =>{
    var body = { banner: this.state.imageBanner };
    fetch(`https://api.aktiv.co.id/api/aktivengine/v1/setting/banner/add`, {
      method: "POST",
      body: JSON.stringify(body),
      headers: new Headers({
        "Content-Type": "application/json",
        Authorization: localStorage.getItem("token"),
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.status == "OK") {
          toast.success("Tambah banner Berhasil", {
            position: "top-right",
            autoClose: 3000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
          this.bannerlist();
          this.toggleStateModal();
        }
      })
      .catch((error) => console.log(error));
  }
  deleteBanner=(id) =>{
    var body = { id:id };
    fetch(`https://api.aktiv.co.id/api/aktivengine/v1/setting/banner/delete`, {
      method: "DELETE",
      body: JSON.stringify(body),
      headers: new Headers({
        "Content-Type": "application/json",
        Authorization: localStorage.getItem("token"),
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.status == "OK") {
          toast.success("Delete banner Berhasil", {
            position: "top-right",
            autoClose: 3000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
          this.bannerlist();
        
        }
      })
      .catch((error) => console.log(error));
  }

  render() {
    return (
      <CContainer>
        <CRow>
          <CCol>
            <CCard>
              <CCardHeader>
                <CCol
                  className={
                    "d-flex justify-content-between align-items-center"
                  }
                >
                  <strong>List Banner</strong>
                  <CButton
                    className="btn-facebook btn-bran"
                    onClick={() => {
                      this.toggleStateModal();
                    }}
                  >
                    <span>Tambah</span>
                  </CButton>
                </CCol>
              </CCardHeader>
              <CCardBody>
                <CDataTable
                  items={this.state.bannerList}
                  fields={[{key:"BannerKe",width:"50%"}, "banner","action"]}
                  itemsPerPage={5}
                  tableFilter
                  sorter
                  pagination
                  scopedSlots={{
                    BannerKe: (item, index) =>(
          
                        <td>{index + 1}</td>
                     
                      ),
                      banner: (item) => (
                      <td>
                        <CImg src={item.banner} width="50%"/>
                      </td>
                    ),
                    action: (item) => (
                      <td>
                        <CButton
                          color="danger"
                          size="sm"
                          onClick={() => {
                            // console.log(item);
                            this.deleteBanner(item.id);
                          }}
                        >
                          <CIcon name="cil-trash" />
                        </CButton>
                      </td>
                    ),
                  }}
                />
              </CCardBody>
            </CCard>
            <ToastContainer
              position="top-right"
              autoClose={5000}
              hideProgressBar={false}
              newestOnTop={false}
              closeOnClick
              rtl={false}
              pauseOnFocusLoss
              draggable
              pauseOnHover
            />
          </CCol>
        </CRow>
        <CModal
          show={this.state.stateModal}
          onClose={this.toggleStateModal}
          size="lg"
        >
          <CModalHeader closeButton>
            <CModalTitle>Tambah Banner</CModalTitle>
          </CModalHeader>
          <CModalBody>
            <CContainer>
              <CCard>
                <CCardHeader>
                  <strong>Banner</strong>
                </CCardHeader>
                <CCardBody>
                  <div className="container-fluid">
                    <div className="row">
                      <div className="col">
                        {this.state.imageBanner == "" && (
                          <div style={styles1}>
                            <FileDrop
                              onFrameDragEnter={(event) =>
                                console.log("onFrameDragEnter", event)
                              }
                              onFrameDragLeave={(event) =>
                                console.log("onFrameDragLeave", event)
                              }
                              onFrameDrop={(event) =>
                                console.log("onFrameDrop", event)
                              }
                              onDragOver={(event) =>
                                console.log("onDragOver", event)
                              }
                              onDragLeave={(event) =>
                                console.log("onDragLeave", event)
                              }
                              onDrop={(files, event) => {
                                console.log("onDrop!", files, event);
                                let data = files[0];
                                console.log(data);
                                this.convertToBase64(data);
                              }}
                              onChange={this.handleInputChange.bind(this)}
                            >
                              <CImg src="logo/image.svg" />
                              Upload image: png, jpg max 500 Kb
                            </FileDrop>
                          </div>
                        )}
                        {this.state.imageBanner != "" && (
                          <FileDrop
                            onFrameDragEnter={(event) =>
                              console.log("onFrameDragEnter", event)
                            }
                            onFrameDragLeave={(event) =>
                              console.log("onFrameDragLeave", event)
                            }
                            onFrameDrop={(event) =>
                              console.log("onFrameDrop", event)
                            }
                            onDragOver={(event) =>
                              console.log("onDragOver", event)
                            }
                            onDragLeave={(event) =>
                              console.log("onDragLeave", event)
                            }
                            onDrop={(files, event) => {
                              console.log("onDrop!", files, event);
                              let data = files[0];
                              console.log(data);
                              this.convertToBase64(data);
                            }}
                            onChange={this.handleInputChange.bind(this)}
                          >
                            <CImg width="50%" src={this.state.imageBanner} />
                          </FileDrop>
                        )}
                      </div>
                    </div>
                  </div>
                </CCardBody>
              </CCard>
            </CContainer>
          </CModalBody>
          <CModalFooter>
            <CButton
              className="btn-facebook btn-brand mt-4 mr-2"
              onClick={() => {
                this.addBanner();
              }}
            >
              <span className="mfs-2">Tambah</span>
            </CButton>
          </CModalFooter>
        </CModal>
      </CContainer>
    );
  }
}
